.example {
  position: relative;
  padding: 25px 20px 40px 20px;

  background-color: @gray;

  @media (min-width: @tablet-width) {
    padding: 43px 30px 56px 30px;

    background-color: @gray;
    box-shadow: inset 0 619px 0 0 #ffffff;
  }

  @media (min-width: @desktop-width) {
    padding: 43px 110px 75px 110px;

    background-color: @gray;
    box-shadow: inset 0 179px 0 0 #ffffff;
  }
}

.example__title {
  margin: 0;
  padding: 0;
  margin-bottom: 40px;

  @media (min-width: @tablet-width) {
    margin-bottom: 63px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 146px;
  }
}

.example__text {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    margin-bottom: 54px;
  }

  @media (min-width: @desktop-width) {
    width: 436px;
    margin-bottom: 69px;
  }
}

.example__indicators {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin-bottom: 40px;

  @media (min-width: @tablet-width) {
    justify-content: flex-start;

    margin-bottom: 80px;
  }

  @media (min-width: @desktop-width) {
    width: 436px;
    margin-bottom: 0;
  }
}

.example__slider {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: @desktop-width) {
    position: absolute;
    top: 0;
    left: 50%;
  }
}
